.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* My css */
#title {
  margin-top: 15vh;
  margin-bottom: 30px;
  color: aqua;
}
.mt-10 {
  margin-top: 10px;
}
.accountName {
  padding-bottom: 10px;
}
#balance {
  padding-bottom: 0.6em;
}
button {
  width: 100%;
}
#aside {
  position: absolute;
  left: 41.5%;
  top: 32%;
}
#bside {
  position: absolute;
  right: 5%;
  top: 32%;
}

.pos {
  color: green;
  transition: 1s;
  transform: translateY(-30px);
}
.neg {
  color: red;
  transition: 1s;
  transform: translateY(30px);
}
.noVis {
  visibility: hidden;
  position: absolute;
}
.borderR {
  border-right: black solid 1px;
}
#borderR {
  border-right: black solid 1px;
}
.error {
  margin: 0;
  color: red;
  font-size: 0.9em;
}
.historyPos {
  font-size: 0.8em;
  color: green;
  margin: 0;
  margin-bottom: 0.4em;
  border-bottom: 1px solid rgb(185, 185, 185);
}
.historyNeg {
  font-size: 0.8em;
  color: red;
  margin: 0;
  margin-bottom: 0.4em;
  border-bottom: 1px solid rgb(185, 185, 185);
}
.open {
  display: block;
}
.closed {
  display: none;
}
